import generatePassword from "./generators.js";

const generatePWD= document.querySelector('.generated-password')
const amountCharacters = document.querySelector('#amount')
const chkUppercase = document.querySelector('#uppercase')
const chkLowercase = document.querySelector('#lowercase')
const chkNumber = document.querySelector('#add-numbers')
const chkSymbol = document.querySelector('#add-symbols')
const btn = document.querySelector('.generate')

export default () => {
  btn.addEventListener('click', () => {
      generatePWD.innerHTML = generate()
  })
}

function generate() {
  const pwd = generatePassword(
    amountCharacters.value, 
    chkUppercase.checked, 
    chkLowercase.checked, 
    chkNumber.checked, 
    chkSymbol.checked
  )

  return pwd || 'Nada selecionado.'
}